\n                overflow: hidden;\n                max-height: 100%;\n            }\n        }\n    }\n}\n\n/* Стили для обертки для выравнивания по центру и задания максимальной ширины контента */\n.wrapper {\n    max-width: 1366px;\n    min-width: 320px;\n    margin: 0 auto;\n    padding: 0 @wrapper-side-padding;\n    box-sizing: border-box;\n}\n\n/* Стили для контейнера с шапкой и контентом для прижатия подвала вниз страницы\nПсевдоэлемент .clr:after задает отступ снизу чтобы контент не налазил на подвал */\n.header-and-main {\n    min-height: 100%;\n\n    &.clr {\n        &:after {\n            content: '';\n            display: block;\n            height: 64px;\n        }\n    }\n}\n\n/* Стили для заголовков разделов */\n.section-heading {\n    font-size: 18px;\n    margin: 0 0 20px 0;\n}\n\n/* Стили для шапки сайта */\n.page_header {\n    border-bottom: 1px solid #f0f0f0;\n\n    .logo-and-nav {\n        padding: 22px 0;\n        display: flex;\n        align-items: center;\n        flex-wrap: wrap;\n    }\n\n    .logo {\n        a {\n            display: block;\n            padding: 2px 0 5px 0;\n        }\n    }\n\n    .menu-btn {\n        width: 30px;\n        height: 30px;\n        background: url(../img/icon_list.svg) no-repeat center;\n        border: none;\n        margin-left: auto;\n        display: none;\n    }\n\n    .menu {\n        display: flex;\n        list-style: none;\n\n        a {\n            font-size: 16px;\n            color: @font-color-primary;\n            line-height: 20px;\n            text-decoration: none;\n            font-weight: 700;\n            opacity: .6;\n            padding: 5px;\n            margin: 0 10px;\n            transition: opacity .2s ease-in-out;\n\n            &:not(:last-child) {\n                margin-right: 8px;\n            }\n\n            &:hover {\n                opacity: 1;\n            }\n        }\n\n        .active {\n            opacity: 1;\n        }\n    }\n}\n\n/* Стили для плашки сценариев и устройств */\n.billet {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-end;\n    width: 200px;\n    height: 120px;\n    background: @button-color-primary;\n    border-radius: @border-radius-primary;\n    padding: 14px;\n    box-sizing: border-box;\n    position: relative;\n    background-position: 14px 14px;\n    background-repeat: no-repeat;\n    text-align: left;\n\n    .name {\n        font-weight: 700;\n        margin: 0;\n        margin-bottom: 4px;\n    }\n\n    .action {\n        font-size: 12px;\n        color: @font-color-second;\n        margin: 0;\n    }\n\n    &.icon_sun {\n        background-image: url(../img/icon_sun.svg);\n    }\n    \n    &.icon_sun_2 {\n        background-image: url(../img/icon_sun_2.svg);\n    }\n    \n    &.icon_temperature {\n        background-image: url(../img/icon_temperature.svg);\n    }\n    \n    &.icon_temperature_2 {\n        background-image: url(../img/icon_temperature_2.svg);\n    }\n    \n    &.icon_scheduled {\n        background-image: url(../img/icon_scheduled.svg);\n    }\n}\n\n/* Стили для контейнера, содержащего разделы Главное и Избранные сценарии */\n.main-and-scenarios {\n    padding: 29px 0;\n    display: flex;\n    justify-content: space-between;\n\n    .section-main,\n    .section-scenarios {\n        width: 49%;\n    }\n\n    .inner-block {\n        width: 100%;\n        height: @main-slider-height;\n        border-radius: @border-radius-primary;\n        display: flex;\n        justify-content: space-between;\n    }\n}\n\n/* Стили для раздела Главное */\n.section-main {\n    .inner-block {\n        background-image: url(../img/bg@1x.png);\n        background-position: center;\n        background-size: cover;\n        background-repeat: no-repeat;\n        padding: 25px;\n        box-sizing: border-box;\n    }\n\n    .greeting {\n        font-size: 27px;\n        font-weight: 700;\n        margin: 0;\n    }\n\n    .status-text {\n        margin: 6px 0 0;\n    }\n\n    .temperature-blocks {\n        margin-top: 25px;\n\n        &:after {\n            content: '';\n            display: inline-block;\n            width: 26px;\n            height: 26px;\n            background-image: url(../img/cloud-drizzle.svg);\n            margin-left: 5px;\n        }\n\n        .block {\n            display: inline-block;\n\n            &:not(:last-child) {\n                margin-right: 15px;\n            }\n\n            .label {\n                color: @font-color-second;\n                margin: 0;\n            }\n\n            .temperature {\n                font-size: 36px;\n                font-weight: 700;\n                margin: 4px 0 0;\n            }\n        }\n    }\n\n    .devices-slider {\n        margin: -25px -5px -25px 0;\n        height: @main-slider-height;\n        width: 200px;\n        min-width: 200px;\n        overflow: hidden;\n        position: relative;\n    }\n\n    .devices {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n        overflow-y: auto;\n        top: 0;\n        bottom: 0;\n        left: 0;\n        right: -20px;\n        display: flex;\n        flex-direction: column;\n        position: absolute;\n\n        li {\n            margin: 7.5px 0;\n\n            &:first-child {\n                margin-top: 20px;\n            }\n\n            &:last-child {\n                margin-bottom: 20px;\n            }\n\n            &:nth-child(3) .billet:after {\n                content: '';\n                position: absolute;\n                width: 16px;\n                height: 16px;\n                background-image: url(../img/double-arrow-up.svg);\n                left: 50%;\n                margin-left: -8px;\n                top: 13px;\n                transition: opacity .2s ease-in-out;\n            }\n        }\n\n        &.scrolled li:nth-child(3) .billet:after {\n            opacity: 0;\n        }\n    }\n}\n\n/* Стили для раздела Избранные сценарии */\n.section-scenarios {\n    .scenarios-slider {\n        overflow: hidden;\n        padding: 2px;\n        margin: 0px -20px -15px 0px;\n    }\n\n    .heading-and-controls {\n        display: flex;\n\n        .controls {\n            margin-left: auto;\n\n            button {\n                background: none;\n                border: none;\n                opacity: .3;\n                padding: 5px 10px;\n            }  \n        }\n    }\n\n    .scenarios {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: flex-start;\n\n        li {\n            margin: 2px 15px 15px 0;\n        }\n\n        button {\n            border: none;\n            margin: 0;\n            padding: 0;\n            background: none;\n            border-radius: 20px;\n\n            .billet {\n                width: 200px;\n                height: 100px;\n            }\n\n            &:focus {\n                outline: none;\n                box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n            }\n\n            &:hover .billet {\n                background-color: #fff;\n                box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n                border-radius: @border-radius-primary;\n                cursor: pointer;\n            }\n\n            &:active .billet {\n                background-color: #fff;\n                box-shadow: inset 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n            }\n        }\n    }\n}\n\n/* Стили для раздела Избранные устройства */\n.section-fav-devices {\n    .top-bar {\n        display: flex;\n        align-items: center;\n        margin-bottom: 25px;\n\n        .section-heading {\n            margin: 0;\n        }\n    \n        .controls {\n            margin-left: auto;\n    \n            button {\n                background: none;\n                border: none;\n                opacity: .3;\n                padding: 5px 10px;\n            }\n        }\n\n        .filter {\n            padding: 0;\n            margin: 0;\n            margin-left: 3px;\n            list-style: none;\n            display: flex;\n\n            button {\n                border: none;\n                background-color: @button-color-primary;\n                border-radius: @border-radius-primary;\n                padding: 8px 15px;\n                font-size: 13px;\n                color: #000000;\n                font-weight: 700;\n                margin-left: 10px;\n\n                &:hover {\n                    cursor: pointer;\n                    background-color: #FFD93E;\n                }\n\n                &.active {\n                    background-color: #FFD93E;\n                }\n            }\n        }\n    }\n\n    .fav-devices-slider {\n        overflow: hidden;\n        height: 126px;\n        min-height: 126px;\n        margin-right: -@wrapper-side-padding;\n        margin-left: -@wrapper-side-padding;\n    }\n\n    .fav-devices {\n        display: flex;\n        margin: 0;\n        padding: 2px 0 10px;\n        list-style: none;\n        overflow-x: scroll;\n\n        &:before {\n            content: '';\n            display: block;\n            height: 100px;\n            min-width: @wrapper-side-padding;\n        }\n\n        &:after {\n            content: '';\n            display: block;\n            height: 100px;\n            min-width: @wrapper-side-padding;\n        }\n\n        li {\n            margin-right: 15px;\n\n            &:first-child {\n                margin-left: 0;\n            }\n\n            &:last-child {\n                margin-right: 0;\n            }\n\n            button {\n                border: none;\n                margin: 0;\n                padding: 0;\n                background: none;\n                border-radius: 20px;\n\n                &:focus {\n                    outline: none;\n                    box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n                }\n\n                &:hover .billet {\n                    background-color: #fff;\n                    box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n                    border-radius: @border-radius-primary;\n                    cursor: pointer;\n                }\n    \n                &:active .billet {\n                    background-color: #fff;\n                    box-shadow: inset 0 2px 6px 0 rgba(197, 186, 186, 0.90);\n                }\n            }\n        }\n    }\n}\n\n/* Стили для подвала сайта */\n.page_footer {\n    margin-top: -64px;\n\n    .links-and-copy {\n        display: flex;\n        padding: 19px 0;\n    }\n\n    .footer-links {\n        list-style: none;\n        padding: 0;\n        margin: 0;\n        display: flex;\n        flex-wrap: wrap;\n\n        li {\n            a {\n                padding: 5px 10px 5px 0;\n                display: inline-block;\n                font-size: 12px;\n                color: @font-color-second;\n                text-decoration: none;\n                line-height: 16px;\n\n                &:hover {\n                    color: @font-color-primary;\n                }\n            }\n        }\n    }\n    \n    .copy {\n        margin-left: auto;\n        font-size: 12px;\n        color: @font-color-second;\n        line-height: 26px;\n    }\n}\n\n/* Стили для модального окна */\n.modal {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background: rgba(51,51,51,0.50);\n    display: none;\n    justify-content: center;\n    align-items: center;\n\n    &.visible {\n        display: flex;\n    }\n\n    .modal-wrapper {\n        width: 630px;\n        position: absolute;\n        left: 50%;\n        top: 50%;\n        transform: translate(-50%, -50%);\n    }\n\n    .modal-content {\n        background: #fff;\n        border-radius: 20px;\n\n        padding: 25px;\n\n        .top-bar {\n            display: flex;\n\n            .dev {\n                .name {\n                    margin: 0;\n                    margin-bottom: 6px;\n                    font-size: 27px;\n                }\n            }\n\n            .icon {\n                margin-left: auto;\n                font-size: 36px;\n                color: #333333;\n                font-weight: 700;\n\n                &:after {\n                    content: '';\n                    display: inline-block;\n                    width: 40px;\n                    height: 40px;\n                    background-repeat: none;\n                    background-position: center;\n                    background-size: cover;\n                    vertical-align: bottom;\n                    margin-bottom: 2px;\n                }\n            }\n\n            .icon-temp.powerOff:after {\n                background-image: url(../img/icon_temperature.svg);\n            }\n\n            .icon-temp:after {\n                background-image: url(../img/icon_temperature_2.svg);\n            }\n        }\n\n        .mode-wrapper {\n            overflow: hidden;\n            height: 30px;\n            max-height: 30px;\n            position: relative;\n            margin: 15px -25px 25px -25px;\n            box-sizing: border-box;\n        }\n\n        .mode {\n            list-style: none;\n            padding: 0;\n            margin: 0;\n            display: flex;\n            overflow-x: scroll;\n            width: 100%;\n            position: absolute;\n\n            &:before {\n                content: '';\n                display: block;\n                height: 30px;\n                min-width: 25px;\n            }\n\n            &:after {\n                content: '';\n                display: block;\n                height: 30px;\n                min-width: 25px;\n            }\n\n            li {\n                display: inline-block;\n                margin-right: 5px;\n\n                &:last-child {\n                    margin-right: 0;\n                }\n            }\n\n            button {\n                background-color: #F2F1F0;\n                border: none;\n                border-radius: 20px;\n                padding: 7px 15px;\n                font-size: 13px;\n                color: #000;\n                margin: 1px;\n\n                &.active, &:hover {\n                    background-color: #FFD93E;\n                    cursor: pointer;\n                }\n            }\n        }\n\n        .temp-slider {\n            border-radius: 30px;\n            height: 60px;\n            overflow: hidden;\n            position: relative;\n\n            &:before {\n                content: '';\n                color: #fff;\n                font-weight: 700;\n                font-size: 18px;\n                position: absolute;\n                left: 20px;\n                top: 50%;\n                transform: translateY(-50%);\n            }\n\n            &:after {\n                content: '';\n                color: #fff;\n                font-weight: 700;\n                font-size: 18px;\n                position: absolute;\n                right: 20px;\n                top: 50%;\n                transform: translateY(-50%);\n            }\n\n            .inner {\n                display: flex;\n                overflow-x: scroll;\n\n                &:before {\n                    content: '';\n                    display: block;\n                    box-sizing: border-box;\n                    min-height: 60px;\n                    min-width: 100%;\n                    margin-right: -60px;\n                }\n\n                &:after {\n                    content: '';\n                    display: block;\n                    box-sizing: border-box;\n                    min-height: 60px;\n                    min-width: 100%;\n                    margin-left: -60px;\n                }\n\n                .handler {\n                    display: inline-block;\n                    min-width: 60px;\n                    min-height: 60px;\n                    box-sizing: border-box;\n                    border-radius: 50%;\n                    border: 3px solid rgba(51,51,51,0.10);\n                    box-shadow: 0 0 0 4px #fff;\n                    position: relative;\n                    z-index: 1;\n                    \n                    &:hover {\n                        cursor: pointer;\n                    }\n                }\n            }\n        }\n\n        .round-slider {\n            position: relative;\n            width: 220px;\n            height: 220px;\n            margin: 20px auto 0;\n            border-radius: 50%;\n\n            .block {\n                background-size: 100%;\n                position: absolute;\n                height: 100%;\n                width: 50%;\n                border-top-left-radius: 110px;\n                border-bottom-left-radius: 110px;\n                transform-origin: right center;\n                z-index: 2;\n            }\n\n            &:after {\n                content: attr(data-after);\n                padding-left: 50px;\n                line-height: 220px;\n                height: 100%;\n                width: 100%;\n                display: block;\n                background: url(../img/termo.svg);\n                position: absolute;\n                z-index: 3;\n                top: 0;\n                left: 0;\n                font-family: Arial, sans-serif;\n                font-weight: 700;\n                font-size: 66px;\n                color: #333333;\n            }\n\n            .pointer {\n                content: '';\n                text-align: center;\n                display: block;\n                position: absolute;\n                z-index: 4;\n                bottom: 16px;\n                left: 50%;\n                margin-left: -6px;\n                border: 6px solid transparent;\n                border-top: 6px solid #333;\n                transform: rotate(290deg);\n                transform-origin: center -82px;\n            }\n\n            .block-1 {\n                background-color: #F5A623;\n            }\n\n            .block-2 {\n                background-color: #F5A623;\n                transform: rotate(110deg);\n                text-align: right;\n            }\n\n            .block-3 {\n                background-color: #333333;\n                transform: rotate(290deg);\n                opacity: 0;\n            }\n\n            .block-4 {\n                background-color: #333333;\n                transform: rotate(180deg);\n                z-index: 1;\n            }\n        }\n    }\n\n    .modal-dialog {\n        display: flex;\n        justify-content: space-between;\n        margin-top: 20px;\n\n        button {\n            background-color: #fff;\n            border: none;\n            border-radius: 20px;\n            width: 307px;\n            padding: 20px 0;\n            text-align: center;\n            font-size: 18px;\n            color: #333;\n            font-weight: 700;\n\n            &:hover {\n                cursor: pointer;\n                color: #000;\n            }\n\n            &.save-modal {\n                background-color: #FFD93E;\n                color: #000;\n\n                &:hover {\n                    background-color: #FFCD00;\n                }\n            }\n        }\n    }\n\n    &.temp-modal {\n        .temp-slider {\n            background-image: linear-gradient(-90deg, #F76B1C 2%, #FFD800 51%, #068FFF 100%);\n\n            &:before {\n                content: '-10';\n            }\n\n            &:after {\n                content: '+30';\n            }\n        }\n    }\n    \n    &.light-modal {\n        .temp-slider {\n            background-image: linear-gradient(-85deg, #FF9E00 2%, #FFE682 98%);\n\n            &:before {\n                width: 40px;\n                height: 40px;\n                left: 10px;\n                background: url(../img/icon_sun_min.svg) no-repeat center;\n            }\n\n            &:after {\n                width: 40px;\n                height: 40px;\n                right: 10px;\n                background: url(../img/icon_sun_max.svg) no-repeat center;\n            }\n        }\n\n        .icon-light.powerOff:after {\n            background-image: url(../img/icon_sun.svg);\n        }\n\n        .icon-light.powerOn:after {\n            background-image: url(../img/icon_sun_2.svg);\n        }\n    }\n}\n\n/* Стили для устройств с плотностью пикселей x2 */\n@media @retina {\n    .section-main {\n        .inner-block {\n            background-image: url(../img/bg@2x.png);\n        }\n    }\n}\n\n/* Стили для устройств с экраном меньше 992px */\n@media @phone {\n    @main-slider-height: 282px;\n    @wrapper-side-padding: 20px;\n\n    .wrapper {\n        padding: 0 @wrapper-side-padding;\n    }\n\n    .header-and-main {\n        padding-top: 64px;\n        transition: padding .2s ease-in-out;\n\n        &.menu-visible {\n            padding-top: 184px;\n        }\n    }\n\n    .page_header {\n        border-bottom: none;\n        position: fixed;\n        top: 0;\n        width: 100%;\n        background-color: #fff;\n        z-index: 1;\n\n        .logo-and-nav {\n            padding: 8px 0;\n        }\n\n        .menu-btn {\n            display: block;\n            order: 0;\n        }\n    \n        nav {\n            order: 1;\n            width: 100%;\n        }\n\n        .menu {\n            flex-direction: column;\n            text-align: center;\n            padding: 0;\n            margin: 0;\n            opacity: 0;\n            height: 0;\n            transition: opacity .2s ease-in-out, height .2s ease-in-out;\n            overflow: hidden;\n\n            a {\n                display: block;\n                margin: 0;\n                font-size: 20px;\n                padding: 10px;\n            }\n\n            &.visible {\n                opacity: 1;\n                height: 120px;\n            }\n        }\n    }\n\n    .main-and-scenarios {\n        flex-direction: column;\n        padding-top: 0;\n\n        .section-main,\n        .section-scenarios {\n            width: 100%;\n        }\n\n        .inner-block {\n            height: @main-slider-height;\n        }\n    }\n\n    .section-main {\n        .section-heading {\n            visibility: hidden;\n            height: 0;\n            margin: 0;\n        }\n\n        .greeting {\n            font-size: 18px;\n        }\n\n        .status-text {\n            font-size: 12px;\n        }\n\n        .temperature-blocks {\n            margin-top: 15px;\n\n            &:after {\n                height: 20px;\n                width: 20px;\n                margin-left: -5px;\n                background-size: 100%;\n            }\n\n            .block {\n                .label {\n                    font-size: 12px;\n                }\n    \n                .temperature {\n                    font-size: 24px;\n                }\n            }\n        }\n\n        .inner-block {\n            flex-direction: column;\n            padding: 20px;\n        }\n\n        .devices-slider  {\n            height: 120px;\n            min-height: 120px;\n            width: auto;\n            min-width: auto;\n            margin: 0px -20px 0 -20px;\n        }\n\n        .devices {\n            flex-direction: row;\n            overflow-x: auto;\n            position: static;\n\n            &:after {\n                content: '';\n                display: block;\n                height: 100px;\n                min-width: @wrapper-side-padding;\n            }\n\n            li {\n                margin: 0 10px;\n\n                &:first-child {\n                    margin-top: 0;\n                    margin-left: 20px;\n                }\n\n                &:last-child {\n                    margin-bottom: 0;\n                    margin-right: 0;\n                }\n\n                &:nth-child(3) .billet:after { \n                    display: none;\n                }\n            }\n        }\n    }\n\n    .section-scenarios {\n        margin-top: 30px;\n\n        .inner-block {\n            height: auto;\n        }\n\n        .scenarios-slider {\n            margin: 0;\n            padding: 0;\n            height: 100px;\n            padding-bottom: 5px;\n            margin-right: -@wrapper-side-padding;\n            margin-left: -@wrapper-side-padding;\n        }\n\n        .heading-and-controls {\n            .controls {\n                display: none;\n            }\n        }\n\n        .scenarios {\n            flex-wrap: nowrap;\n            overflow-y: auto;\n\n            &:before {\n                content: '';\n                display: block;\n                height: 100px;\n                min-width: @wrapper-side-padding;\n            }\n\n            &:after {\n                content: '';\n                display: block;\n                height: 100px;\n                min-width: @wrapper-side-padding;\n            }\n            \n            li {\n                margin: 2px 15px 5px 0;\n\n                &:first-child {\n                    margin-left: 0;\n                }\n    \n                &:last-child {\n                    margin-right: 0;\n                }\n            }\n        }\n    }\n\n    .section-fav-devices {\n        .fav-devices-slider {\n            margin-right: -@wrapper-side-padding;\n            margin-left: -@wrapper-side-padding;\n        }\n\n        .fav-devices {\n            &:before {\n                content: '';\n                display: block;\n                height: 100px;\n                min-width: @wrapper-side-padding;\n            }\n\n            &:after {\n                content: '';\n                display: block;\n                height: 100px;\n                min-width: @wrapper-side-padding;\n            }\n\n            li {\n                &:first-child {\n                    margin-left: 0;\n                }\n    \n                &:last-child {\n                    margin-right: 0;\n                }\n            }\n        }\n\n        .top-bar {\n            .filter {\n                display: none;\n            }\n\n            .controls {\n                display: none;\n            }\n        }\n    }\n\n    .modal {\n        padding: 20px;\n\n        .modal-wrapper {\n            position: static;\n            width: 334px;\n            height: 100%;\n            box-sizing: border-box;\n            transform: none;\n            display: flex;\n            flex-direction: column;\n            justify-content: space-between;\n            \n        }\n\n        .modal-content {\n            padding: 20px;\n            flex: 1 1 auto;\n            display: flex;\n            flex-direction: column;\n\n            .top-bar {\n                .dev {\n                    .name {\n                        font-size: 18px;\n                    }\n\n                    .status {\n                        font-size: 12px;\n                    }\n                }\n\n                .icon {\n                    display: none;\n                }\n            }\n\n            .mode-wrapper {\n                margin: 15px -@wrapper-side-padding 25px -@wrapper-side-padding;\n            }\n\n            .mode {\n                &:before {\n                    content: '';\n                    display: block;\n                    height: 30px;\n                    min-width: @wrapper-side-padding;\n                }\n    \n                &:after {\n                    content: '';\n                    display: block;\n                    height: 30px;\n                    min-width: @wrapper-side-padding;\n                }\n            }\n\n            .temp-slider {\n                width: 60px;\n                flex: 1 1 auto;\n                align-self: center;\n                background-image: linear-gradient(180deg, #F76B1C 2%, #FFD800 51%, #068FFF 100%);\n            \n                &:before {\n            