rapper {\n  height: 100%;\n}\nhtml body .page-wrapper.blur {\n  filter: blur(3px);\n  overflow: hidden;\n  max-height: 100%;\n}\n/* Стили для обертки для выравнивания по центру и задания максимальной ширины контента */\n.wrapper {\n  max-width: 1366px;\n  min-width: 320px;\n  margin: 0 auto;\n  padding: 0 40px;\n  box-sizing: border-box;\n}\n/* Стили для контейнера с шапкой и контентом для прижатия подвала вниз страницы\nПсевдоэлемент .clr:after задает отступ снизу чтобы контент не налазил на подвал */\n.header-and-main {\n  min-height: 100%;\n}\n.header-and-main.clr:after {\n  content: '';\n  display: block;\n  height: 64px;\n}\n/* Стили для заголовков разделов */\n.section-heading {\n  font-size: 18px;\n  margin: 0 0 20px 0;\n}\n/* Стили для шапки сайта */\n.page_header {\n  border-bottom: 1px solid #f0f0f0;\n}\n.page_header .logo-and-nav {\n  padding: 22px 0;\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n}\n.page_header .logo a {\n  display: block;\n  padding: 2px 0 5px 0;\n}\n.page_header .menu-btn {\n  width: 30px;\n  height: 30px;\n  background: url(../img/icon_list.svg) no-repeat center;\n  border: none;\n  margin-left: auto;\n  display: none;\n}\n.page_header .menu {\n  display: flex;\n  list-style: none;\n}\n.page_header .menu a {\n  font-size: 16px;\n  color: #333;\n  line-height: 20px;\n  text-decoration: none;\n  font-weight: 700;\n  opacity: 0.6;\n  padding: 5px;\n  margin: 0 10px;\n  transition: opacity 0.2s ease-in-out;\n}\n.page_header .menu a:not(:last-child) {\n  margin-right: 8px;\n}\n.page_header .menu a:hover {\n  opacity: 1;\n}\n.page_header .menu .active {\n  opacity: 1;\n}\n/* Стили для плашки сценариев и устройств */\n.billet {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-end;\n  width: 200px;\n  height: 120px;\n  background: #F7F7F7;\n  border-radius: 20px;\n  padding: 14px;\n  box-sizing: border-box;\n  position: relative;\n  background-position: 14px 14px;\n  background-repeat: no-repeat;\n  text-