r .icon:after {\n  content: '';\n  display: inline-block;\n  width: 40px;\n  height: 40px;\n  background-repeat: none;\n  background-position: center;\n  background-size: cover;\n  vertical-align: bottom;\n  margin-bottom: 2px;\n}\n.modal .modal-content .top-bar .icon-temp.powerOff:after {\n  background-image: url(../img/icon_temperature.svg);\n}\n.modal .modal-content .top-bar .icon-temp:after {\n  background-image: url(../img/icon_temperature_2.svg);\n}\n.modal .modal-content .mode-wrapper {\n  overflow: hidden;\n  height: 30px;\n  max-height: 30px;\n  position: relative;\n  margin: 15px -25px 25px -25px;\n  box-sizing: border-box;\n}\n.modal .modal-content .mode {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n  display: flex;\n  overflow-x: scroll;\n  width: 100%;\n  position: absolute;\n}\n.modal .modal-content .mode:before {\n  content: '';\n  display: block;\n  height: 30px;\n  min-width: 25px;\n}\n.modal .modal-content .mode:after {\n  content: '';\n  display: block;\n  height: 30px;\n  min-width: 25px;\n}\n.modal .modal-content .mode li {\n  display: inline-block;\n  margin-right: 5px;\n}\n.modal .modal-content .mode li:last-child {\n  margin-right: 0;\n}\n.modal .modal-content .mode button {\n  background-color: #F2F1F0;\n  border: none;\n  border-radius: 20px;\n  padding: 7px 15px;\n  font-size: 13px;\n  color: #000;\n  margin: 1px;\n}\n.modal .modal-content .mode button.active,\n.modal .modal-content .mode button:hover {\n  background-color: #FFD93E;\n  cursor: pointer;\n}\n.modal .modal-content .temp-slider {\n  border-radius: 30px;\n  height: 60px;\n  overflow: hidden;\n  position: relative;\n}\n.modal .modal-content .temp-slider:before {\n  content: '';\n  color: #fff;\n  font-weight: 700;\n  font-size: 18px;\n  position: absolute;\n  left: 20px;\n  top: 50%;\n  transform: translateY(-50%);\n}\n.modal .modal-content .temp-slider:after {\n  content: '';\n  color: #fff;\n  font-weight: 700;\n  font-size: 18px;\n  position: absolute;\n  right: 20px;\